@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital@1&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Abel&family=Playfair+Display:ital@1&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Abel&family=Lobster&family=Playfair+Display:ital@1&display=swap");

html {
  display: flex;
  flex-wrap: wrap;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
/* .App {
  display: flex;
  flex-wrap: wrap;
} */

.nav_1 {
  background: #232a34;
  background: var(--color-background, #232a34);
  top: 0;
  left: 0;
  right: 0;
  min-height: 66px;
  padding: 0 1.15rem;
  z-index: 30;
}
.Unorder {
  display: flex;
  list-style: none;
  margin: 0%;
}
.text {
  font-size: 3.5rem;
  font-weight: bold;
  font-family: "Playfair Display", serif;
  color: var(--color-foreground, #fff);
  margin-top: 0%;
  margin-left: 0%;
}
.input {
  margin-left: 100px;
  padding: 1rem;
  padding-left: 100px;
  padding-right: 200px;
  margin-bottom: 0%;
  border: none;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  border-right-color: transparent;
  font-size: 20px;
  margin-top: 10px;
  margin-bottom: 0;
  -webkit-box-flex: 1;
  z-index: 1;
  font-weight: 300;
  text-transform: capitalize;
  font-family: "Abel", sans-serif;
}
::placeholder {
  font-size: 25px;
  text-transform: capitalize;
  font-family: "Abel", sans-serif;
}
::-moz-placeholder {
  font-size: 25px;
  text-transform: capitalize;
  font-family: "Abel", sans-serif;
}
