#enterKey {
  align-items: center;
  font-style: normal;
  font-size: 20px;
  font-weight: 300;
  font-family: "Abel", sans-serif;
  padding: 16px;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  border: none;
  background-color: #fff;
  cursor: pointer;
  margin-top: 10px;
}
.catImages {
  transition: all 1s;
  height: 400px;
  width: 450px;
}
